






































































































import Vue from "vue";
import initWebGL from "./InitWebgl.vue";
import adobeSDK from "./AdobeSDK.vue";
import { videoPlayer } from "vue-video-player";
import "video.js/dist/video-js.css";
import { mvFunctions } from "../assets/webgl/index";
export default Vue.extend({
  data: () => ({
    init: "check",
    windowHeight: 0,
    windowWidth: 0,
    paintHidden: false,
    rimHidden: false,
    transition: "slide-y-reverse-transition",
    carPaint: [
      { name: "polar white", code: "149U" },
      { name: "brilliant blue", code: "896U" },
      { name: "graphite grey", code: "831U" },
      { name: "high-tech silver", code: "922U" },
      { name: "mojave silver", code: "859U" },
      { name: "obsidian black", code: "197U" },
      { name: "designo hyacinth red metallic", code: "996U" }
    ],
    carRim: [
      {
        name: "48.3 cm (19-inch) 5-twin-spoke light-alloy wheels",
        code: "84R"
      },
      {
        name: "optional 48.3 cm (19-inch) 5-twin-spoke light-alloy wheels",
        code: "R90"
      },
      {
        name: "48.3 cm (19-inch) 5-twin-spoke light-alloy wheels",
        code: "R37"
      }
    ],
    playerOptions: {
      autoplay: false, //true,
      muted: false, // 
      preload: "auto", // <video>auto,
      language: "zh-CN",
      aspectRatio: "16:9", //  - "16:9""4:3"
      fluid: true, // trueVideo.js player
      sources: [
        {
          src: "./video/car_makingof.m4v", // 
          type: "video/mp4" // 
        }
      ],
      poster: "../../static/images/test.jpg", //
      // width: document.documentElement.clientWidth,
      notSupportedMessage: "", //Video.js
      controlBar: {
        timeDivider: true,
        durationDisplay: true,
        remainingTimeDisplay: false,
        fullscreenToggle: true //
      }
    }
  }),

  components: {
    initWebGL,
    videoPlayer,
    adobeSDK
  },
  watch: {
    windowHeight: function() {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
    }
  },
  methods: {
    onResize() {
      return window.innerHeight + "px";
    },
    changeCarpaint(code: string) {
      mvFunctions.switchConfiguration(code);

      this.paintHidden = !this.paintHidden;
    },
    changeRims(code: string) {
      console.log("rims changed" + code);
      mvFunctions.switchConfiguration(code);
      this.rimHidden = !this.rimHidden;
    }
  }
});
