import * as BABYLON from "babylonjs";
console.log("load postProcess.js");

export function createPostProcess(engine, scene, camera) {
  const isPostprocessEnabled = false;
  const isLUTEnabled = false;
  if (isPostprocessEnabled) {
    const defaultPipeline = new BABYLON.DefaultRenderingPipeline(
      "default",
      true,
      scene,
      [camera]
    );
    //   defaultPipeline.bloomEnabled = false;
    defaultPipeline.fxaaEnabled = true;
    defaultPipeline.samples = 8;
    //   defaultPipeline.bloomWeight = 0.5;

    if (isLUTEnabled) {
      const postProcess = new BABYLON.ColorCorrectionPostProcess(
        "color_correction",
        "assets/textures/lut-highcontrast.png",
        1.0,
        camera
      );
    }
  }
}
