let initialMaterials = [];
const product = "eqc";
const assetsPath = "./webgl/assets/";
const entityConfigPath = `./webgl/assets/${product}_entity_config.json`;
const paths = {
  assetsPath: "./webgl/assets/",
  entityConfigPath: `./webgl/assets/${product}_entity_config.json`,
  materialsPath: "",
  texturesPath: "",
  meshesPath: "",
  ruleEnginePath: "",
  materialMappingPath: "",
};

let ruleEngine;
let materialMapping;
let configurationInfo;
let configurableLayers = [{}];
let nonConfigurableLayers = [{}];
let camera;

let interestPointsMeshes = [];

let scene;

const showDebugLayer = false;

function enableSSRReflections() {
  const floorMat = scene.getMeshByName("EQC_ENV_Floor")?.material;
  floorMat.reflectivityTexture = new BABYLON.Texture(
    "./webgl/assets/textures/reflectivity.png",
    scene
  );
  const ssr = new BABYLON.ScreenSpaceReflectionPostProcess(
    "ssr",
    scene,
    1.0,
    camera
  );
  ssr.reflectionSamples = 50; // Low quality.
  ssr.strength = 1; // Set default strength of reflections.
  ssr.reflectionSpecularFalloffExponent = 3; // Attenuate the reflections a little bit. (typically in interval [1, 3])
}

