export function initWebGL() {
  // Get the canvas DOM element
  const canvas = document.getElementById("renderCanvas") as HTMLCanvasElement;
  const loadingScreen = document.getElementById("loading") as HTMLCanvasElement;

  // Load the 3D engine
  const engine = new BABYLON.Engine(canvas, true, {
    preserveDrawingBuffer: true,
    stencil: true,
  });

  // CreateScevarne function that creates and return the scene
  const createScene = function() {
    // Create a basic BJS Scene object
    const scene = new BABYLON.Scene(engine);

    scene.debugLayer.show();

    // Create a camera
    camera = createCamera(scene, canvas);

    //load environment lighting
    loadEnvironmentFromJSON(
      scene,
      "./webgl/assets/abandonedHall_environment_config.json"
    );

    // create post process
    createPostProcess(engine, scene, camera);

    //When click event is raised
    scene.onPointerDown = function(evt, pickResult: any) {
      // We try to pick an object
      if (pickResult.hit) {
        for (const point of interestPointsMeshes) {
          if (pickResult.pickedMesh == point) {
            callActionPoint();
          }
        }
      }
    };

    async function loadAssets() {
      // load paths from entity config JSON
      const entityConfig = await sceneLoader.readJSON(paths.entityConfigPath);
      paths.meshesPath = paths.assetsPath + entityConfig.meshesUrlRelative;
      paths.materialsPath =
        paths.assetsPath + entityConfig.materialsUrlRelative;
      paths.texturesPath = paths.assetsPath + entityConfig.texturesUrlRelative;
      paths.materialMappingPath =
        paths.assetsPath + entityConfig.materialMappingsUrlRelative;
      paths.ruleEnginePath =
        paths.assetsPath + entityConfig.ruleEngineConfigUrlRelative;

      // load rule engine JSON
      ruleEngine = await sceneLoader.readJSON(paths.ruleEnginePath);

      // load material mapping JSON
      materialMapping = await sceneLoader.readJSON(paths.materialMappingPath);

      //const result = await sceneLoader.readProjectJSONS(paths);
      console.log("configuration info loaded");

      // load nonConfigurableLayers
      nonConfigurableLayers = await sceneLoader.loadObjects(
        ruleEngine.nonConfigurableLayers,
        paths,
        scene,
        engine
      );
      console.log("nonConfigurableLayers loaded");

      // load ConfigurableLayers
      configurableLayers = await sceneLoader.loadObjects(
        ruleEngine.configurableLayers,
        paths,
        scene,
        engine
      );

      console.log("configurableLayers loaded");

      initialMaterials = await sceneLoader.initializeMaterials(
        nonConfigurableLayers.concat(configurableLayers),
        materialMapping,
        paths,
        scene
      );

      await sceneLoader.createSwitchMaterials(scene, ruleEngine, paths);
      console.log("switch materials created");

      interestPointsMeshes = interestPoints.initialize(scene);
    }

    // read JSONS
    loadAssets().then(() => {
      console.log("assets loaded");
      sceneLoader.switchConfiguration(
        "R37",
        initialMaterials,
        configurableLayers,
        ruleEngine,
        scene
      );

      scene.registerBeforeRender(function() {
        interestPoints.alignToCamera(interestPointsMeshes, camera);
      });

      if (showDebugLayer == true) {
        const sceneExplorer = document.getElementById(
          "scene-explorer-host"
        ) as HTMLCanvasElement;

        const sceneInspector = document.getElementById(
          "inspector-host"
        ) as HTMLCanvasElement;

        if (sceneExplorer) {
          sceneExplorer.style.position = "absolute";
          sceneExplorer.style.zIndex = "999";
          sceneInspector.style.position = "fixed";
          sceneInspector.style.zIndex = "999";
        }
      }

      setTimeout(() => {
        const floorMat = scene.getMeshByName("EQC_ENV_Floor")?.material;
        floorMat.reflectionTexture = new BABYLON.MirrorTexture(
          "mirror",
          1024,
          scene,
          true
        );

        floorMat.reflectionTexture.mirrorPlane = new BABYLON.Plane(
          0,
          -0.01,
          0,
          0
        );

        // add meshes
        for (const layer of nonConfigurableLayers) {
          for (const mesh of layer.meshes) {
            floorMat.reflectionTexture.renderList.push(mesh);
          }
        }
        for (const layer of configurableLayers) {
          for (const mesh of layer.meshes) {
            floorMat.reflectionTexture.renderList.push(mesh);
          }
        }

        // floorMat.reflectionTexture.renderList = [
        //   scene.getMeshByName("EQC_ENV_Walls_Main"),
        //   scene.getMeshByName("carpaint"),
        //   scene.getMeshByName("EQC_ENV_Walls_Secondary"),
        // ];
        floorMat.reflectionTexture.level = 2;

        floorMat.reflectionTexture.blurKernel = 32;
      }, 1000);
    });

    // Return the created scene
    return scene;
  };

  // call the createScene function
  scene = createScene();

  //** LOAD ASSETS */
  // read JSON config

  //reflection texture

  // run the render loop
  engine.runRenderLoop(function() {
    scene.render();
  });

  // the canvas/window resize event handler
  window.addEventListener("resize", function() {
    engine.resize();
  });
}

export const mvFunctions = {
  switchConfiguration: function(newConfigurationCode) {
    sceneLoader.switchConfiguration(
      newConfigurationCode,
      initialMaterials,
      configurableLayers,
      ruleEngine,
      scene
    );
  },
};

function switchConfiguratioaaan(newConfigurationCode) {
  sceneLoader(newConfigurationCode, initialMaterials, ruleEngine);
}
