import * as BABYLON from "babylonjs";

export function readJSON(path) {
  return new Promise(function(resolve, reject) {
    fetch(path)
      .then(function(resp) {
        return resp.json();
      })

      .then(function(data) {
        if (!data) {
          reject(new Error("Cannot load json"));
        }
        resolve(data);
      });
  });
}

export function readProjectJSONS(paths) {
  return new Promise((resolve, reject) => {
    readJSON(paths.entityConfigPath).then(function(result) {
      paths.meshesPath = paths.assetsPath + result.meshesUrlRelative;
      paths.materialsPath = paths.assetsPath + result.materialsUrlRelative;
      paths.texturesPath = paths.assetsPath + result.texturesUrlRelative;
      paths.materialMappingPath =
        paths.assetsPath + result.materialMappingsUrlRelative;
      paths.ruleEnginePath =
        paths.assetsPath + result.ruleEngineConfigUrlRelative;

      // read the rest of the JSON files, wait until all are read before continuing

      Promise.all([
        readJSON(paths.ruleEnginePath),
        readJSON(paths.materialMappingPath),
      ]).then((results) => {
        resolve(results);
      });
    });
  });
  // read entity config file
}

function createMaterialPromise(scene, newMaterialJSON, texturesPath) {
  return new Promise((resolve, reject) => {
    let newMaterial;

    fetch(newMaterialJSON)
      .then(function(resp) {
        return resp.json();
      })

      .then(function(data) {
        newMaterial = BABYLON.Material.Parse(data, scene, texturesPath);
      })
      .then(function() {
        if (newMaterial != null) {
          1;
          resolve(newMaterial);
        } else reject();
      });
  });
}

export function initializeMaterials(layers, materialMapping, paths, scene) {
  return new Promise(function(resolve, reject) {
    const initialMaterials = [];
    const dict = {};
    materialMapping.materialAllocators.forEach(function(x) {
      dict[x.name] = x.mapping;
    });

    for (const layer of layers) {
      for (const mesh of layer.meshes) {
        if (mesh.material) {
          mesh.isPickable = true;
          const initialMaterial = {
            mesh: "",
            materialSlotName: "",
          };
          initialMaterial.mesh = mesh;
          initialMaterial.materialSlotName = mesh.material.name;
          initialMaterials.push(initialMaterial);

          if (dict[initialMaterial.materialSlotName]) {
            const newMaterialJSON =
              paths.materialsPath + dict[initialMaterial.materialSlotName];
            createMaterialPromise(
              scene,
              newMaterialJSON,
              paths.texturesPath
            ).then(function(result) {
              mesh.material = result;
            });
          }
        }
      }
    }

    resolve(initialMaterials);
  });
}

export function loadObjects(layers, paths, scene, engine) {
  return new Promise((resolve, reject) => {
    const assetsManager = new BABYLON.AssetsManager(scene);

    for (const layer of layers) {
      assetsManager.addMeshTask(
        "carLoading",
        "",
        paths.meshesPath,
        `${layer}.glb`
      );
    }

    assetsManager.onProgress = function(
      remainingCount,
      totalCount,
      lastFinishedTask
    ) {
      engine.loadingUIText =
        "We are loading the scene. " +
        remainingCount +
        " out of " +
        totalCount +
        " items still need to be loaded.";
    };

    assetsManager.onFinish = function(tasks) {
      const layers = [];
      tasks.forEach((task) => {
        const obj = function(layerName, meshes) {
          this.name = layerName;
          this.meshes = meshes;
        };
        const newLayer = new obj(task.sceneFilename, task.loadedMeshes);
        layers.push(newLayer);
      });

      resolve(layers);
    };

    assetsManager.load();
  });
}

export function createSwitchMaterials(scene, ruleEngine, paths) {
  return new Promise((resolve, reject) => {
    for (const property of ruleEngine.properties) {
      if (property.materialSchemes) {
        if (property.materialSchemes[0].switchMaterialSlot) {
          createMaterialPromise(
            scene,
            paths.materialsPath + property.materialSchemes[0].switchMaterialSlot
          );
        }
      }
    }
    resolve();
  });
}

function validateConfiguration(newConfigurationCode, configurationInfo) {
  for (let i = 0; i < configurationInfo.length; i++) {
    if (configurationInfo[i].code === newConfigurationCode) {
      return configurationInfo[i];
    }
  }
}

export function switchConfiguration(
  newConfigurationCode,
  initialMaterials,
  configurableLayers,
  ruleEngine,
  scene
) {
  const validProperty = validateConfiguration(
    newConfigurationCode,
    ruleEngine.properties
  );
  if (validProperty.materialSchemes) {
    for (const sceneMeshes of initialMaterials) {
      console.log(sceneMeshes);

      if (
        sceneMeshes.materialSlotName ===
        validProperty.materialSchemes[0].switchMaterialName
      ) {
        sceneMeshes.mesh.material = scene.getMaterialByName(
          validProperty.materialSchemes[0].switchMaterialSlot
        );
      }
    }
  } else if (validProperty.layerNames) {
    for (const layer of configurableLayers) {
      if (layer.name === validProperty.layerNames + ".glb") {
        for (const mesh of layer.meshes) {
          mesh.isVisible = true;
        }
      } else {
        for (const mesh of layer.meshes) {
          mesh.isVisible = false;
        }
      }
    }
  }
}
