import * as BABYLON from "babylonjs";
console.log("load envLoader.js");

export function loadEnvironmentFromJSON(scene, envJSONPath) {
  const src = document.currentScript.src;
  let environmentConfig;
  const useSkybox = false;
  fetch(envJSONPath)
    .then(function(resp) {
      return resp.json();
    })
    .then(function(data) {
      environmentConfig = data.mappings[0].config;
    })
    .then(function() {
      // setup the environment lighting info
      const hdrTexture = new BABYLON.CubeTexture.CreateFromPrefilteredData(
        "./webgl/assets/textures/" + environmentConfig.environmentTexture.url,
        scene
      );

      hdrTexture.rotationY = environmentConfig.environmentTexture.rotationY;
      scene.environmentTexture = hdrTexture;

      scene.environmentIntensity = environmentConfig.environmentIntensity;
    })
    .then(function() {
      if (useSkybox) {
        // set the background texture
        const envTexture = new BABYLON.CubeTexture(
          "./webgl/assets/textures/" + environmentConfig.environmentTexture.url,
          scene
        );
        scene.createDefaultSkybox(envTexture, true, 1000);
      } else {
        scene.clearColor = new BABYLON.Color3(1, 1, 1);
      }
    });
  return environmentConfig;
}
