// MV interest points manager

import * as BABYLON from "babylonjs";

function asignMaterials(scene) {
  // interest point materials
  const interestPointMat = new BABYLON.StandardMaterial(
    "interestPointMat",
    scene
  );
  interestPointMat.diffuseColor = new BABYLON.Vector3(0, 0, 1);
  interestPointMat.emissiveTexture = new BABYLON.Texture(
    "./webgl/assets/textures/interestPointIcon.png",
    scene
  );
  interestPointMat.opacityTexture = new BABYLON.Texture(
    "./webgl/assets/textures/interestPointIcon.png",
    scene
  );
  interestPointMat.useAlphaFromDiffuseTexture = true;

  interestPointMat.backFaceCulling = false;

  scene.getMeshByName("InterestPoint_001").material = interestPointMat;
}

export function alignToCamera(interestPoints, camera) {
  if (camera != null) {
    const interestPointYaw = camera.alpha;

    const interestPointPitch = camera.beta + 1.5;

    if (interestPointYaw) {
      for (const interestPoint of interestPoints) {
        interestPoint.rotation = new BABYLON.Vector3(
          0,
          interestPointYaw,
          interestPointPitch
        );
      }
    }
  }
}

export function initialize(scene) {
  const interestPoints = [];
  interestPoints.push(scene.getMeshByName("InterestPoint_001"));
  interestPoints.push(scene.getMeshByName("InterestPoint_002"));
  interestPoints.push(scene.getMeshByName("InterestPoint_003"));

  asignMaterials(scene);

  return interestPoints;
}
